export const API_URL = "https://rh50nu19ni.execute-api.us-east-2.amazonaws.com/beta/graphql";
/**
 * flattens a graphQL response to only include relevant structure
 *
 * @param {node} any - an element inside of a graphQL response
 *
 */
export function flatten(node) {
    if (!node) {
        return node;
    }
    else if (Array.isArray(node)) {
        return node.map(flatten);
    }
    else if (typeof node === "object") {
        let props = Object.getOwnPropertyNames(node);
        if (props.length === 1) {
            return flatten(node[props[0]]);
        }
        else if (props.length > 1) {
            const flattened = {};
            for (let prop of props) {
                flattened[prop] = flatten(node[prop]);
            }
            return flattened;
        }
    }
    else {
        return node;
    }
}
export function getPinnedRepoQuery(username = "kclejeune", maxNumRepos = 15, maxNumTopics = 15, maxNumLanguages = 15) {
    return `
{
  repositoryOwner(login: "${username}") {
    ... on ProfileOwner {
      login
      itemShowcase {
        ... on ProfileItemShowcase {
          items(first: ${maxNumRepos}) {
            totalCount
            nodes {
              ... on Repository {
                name
                stargazerCount
                forkCount
                url
                description
                repositoryTopics(first: ${maxNumTopics}) {
                  nodes {
                    ... on RepositoryTopic {
                      topic {
                        name
                      }
                    }
                  }
                }
                languages(first: ${maxNumLanguages}) {
                  nodes {
                    ... on Language {
                      name
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
`;
}
